import React, { useRef } from "react";

import { MouseParallax, ScrollParallax } from "react-just-parallax";
import jsonData from "../../json/data.json";
import { useSnapshot } from "valtio";
import state from "../../store";
const Author = ({ ...props }) => {
  const scrollParallaxRef = useRef(null);
  const snap = useSnapshot(state);
  return (
    <div className="w-full flex relative lg:min-h-[600px]" {...props}>
      <div className="Anuphan flex justify-center flex-row">
        <section className="Anuphan max-w-5xl flex flex-col items-center justify-center p-2 relative sm:p-20 ">
          <p className="mb-20 lg:mb-6 z-10 relative">
            <ScrollParallax strength={0.05} ref={scrollParallaxRef} shouldPause={false}>
              <span className="flex items-center flex-col">
                <span className="text-3xl sm:text-4xl grad-t-1">
                  {props?.title || jsonData.title}
                </span>
                <span className="text-7xl grad-t-2 Alfa mb-6 text-center">
                  {jsonData.name}
                </span>
              </span>
            </ScrollParallax>
          </p>
          <p className="leading-6 text-lg z-[5] relative mb-10 ">
            <span className="inline-block backdrop-blur-md bg-white p-3">
              {jsonData.personal_info.map((text, index) => (
                <span key={index} className="inline-block ml-2">
                  {text}
                </span>
              ))}
            </span>
          </p>
        </section>
        <section className="flex-1 flex relative z-0 justify-center items-center ">
          <MouseParallax strength={0.05} isAbsolutelyPositioned={true}>
            <div
              className="portraitRight xl:portraitLeft  -rotate-[15deg]"
              name="tailwind poloriod"
            >
              <img
                className="absolute w-full h-full left-0 top-0 p-10 object-contain object-right-top blur-[.1px] contrast-200 brightness-200 xl:blur-[2px]"
                src={snap.authorsPic}
                alt="author portrait"
                title="picture of author"
              />
            </div>
          </MouseParallax>
        </section>
      </div>
    </div>
  );
};

export default Author;
